import React from 'react';
import '../styles/MainPage.css';
import BootstrapAlert from '../components/BootstrapAlert';
import BootStrapToggleText from '../components/BootStrapToggleText';

const posts = [
  { id: 1, title: '  ', date: '2024-01-01', excerpt: '    .', slug: 'first-post' },
  { id: 2, title: '  ', date: '2024-02-01', excerpt: '    .', slug: 'second-post' }
];

const MainPage = () => {
  return (
    <div className="main-page">
      <h2>  </h2>
      <div className="post-list">
        {posts.map((post) => (
          <div className="post-card" key={post.id}>
            <h3>{post.title}</h3>
            <p>{post.date}</p>
            <p>{post.excerpt}</p>
            <a href={`/posts/${post.slug}`} className="read-more"></a>
          </div>
        ))}
      </div>
      <br />
      <BootstrapAlert
        openBtnText="" 
        modalTitle=" " 
        modalContent=" ." 
        closeBtnText="" 
        />
      <br />
      <BootstrapAlert
        openBtnText="2" 
        modalTitle=" 2" 
        modalContent=" .2" 
        closeBtnText="" 
        />
      <br />
      <BootStrapToggleText
        openBtnText=""
        contents="     ."
        />
    </div>
  );
};

export default MainPage;